import React from 'react'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { motion } from 'framer-motion'
import { 
  Star, 
  Clock, 
  Users, 
  Play, 
  Heart, 
  ShoppingCart,
  Award,
  TrendingUp
} from 'lucide-react'
import { useCart } from '../../contexts/CartContext'
import { useWishlist } from '../../contexts/WishlistContext'
import { useAuth } from '../../contexts/AuthContext'

export default function CourseCard({ course, view = 'grid' }) {
  const { t } = useTranslation()
  const { addToCart, isInCart } = useCart()
  const { addToWishlist, removeFromWishlist, isInWishlist } = useWishlist()
  const { isAuthenticated } = useAuth()

  const handleAddToCart = (e) => {
    e.preventDefault()
    e.stopPropagation()
    if (!isAuthenticated) {
      // Redirect to login
      return
    }
    addToCart(course)
  }

  const handleWishlistToggle = (e) => {
    e.preventDefault()
    e.stopPropagation()
    if (!isAuthenticated) {
      // Redirect to login
      return
    }
    if (isInWishlist(course.id)) {
      removeFromWishlist(course.id)
    } else {
      addToWishlist(course)
    }
  }

  const formatPrice = (price) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(price)
  }

  const formatNumber = (num) => {
    if (num >= 1000000) {
      return (num / 1000000).toFixed(1) + 'M'
    }
    if (num >= 1000) {
      return (num / 1000).toFixed(1) + 'K'
    }
    return num.toString()
  }

  if (view === 'list') {
    return (
      <motion.div
        whileHover={{ y: -2 }}
        className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 overflow-hidden hover:shadow-lg transition-shadow"
      >
        <Link to={`/course/${course.id}`} className="flex">
          <div className="relative w-48 h-32 flex-shrink-0">
            <img
              src={course.thumbnail || course.image}
              alt={course.title}
              className="w-full h-full object-cover"
            />
            {course.isBestseller && (
              <div className="absolute top-2 left-2 bg-yellow-500 text-white text-xs px-2 py-1 rounded">
                Bestseller
              </div>
            )}
            {course.isNew && (
              <div className="absolute top-2 left-2 bg-green-500 text-white text-xs px-2 py-1 rounded">
                New
              </div>
            )}
            <div className="absolute inset-0 bg-black bg-opacity-0 hover:bg-opacity-20 transition-all duration-200 flex items-center justify-center">
              <Play className="w-8 h-8 text-white opacity-0 hover:opacity-100 transition-opacity" />
            </div>
          </div>
          
          <div className="flex-1 p-6">
            <div className="flex justify-between items-start mb-2">
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-1 line-clamp-2">
                  {course.title}
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  {course.instructor}
                </p>
              </div>
              <div className="flex items-center space-x-2 ml-4">
                <button
                  onClick={handleWishlistToggle}
                  className={`p-2 rounded-full transition-colors ${
                    isInWishlist(course.id)
                      ? 'text-red-500 bg-red-50 dark:bg-red-900/20'
                      : 'text-gray-400 hover:text-red-500 hover:bg-red-50 dark:hover:bg-red-900/20'
                  }`}
                >
                  <Heart className="w-4 h-4" fill={isInWishlist(course.id) ? 'currentColor' : 'none'} />
                </button>
                <button
                  onClick={handleAddToCart}
                  disabled={isInCart(course.id)}
                  className={`p-2 rounded-full transition-colors ${
                    isInCart(course.id)
                      ? 'text-green-500 bg-green-50 dark:bg-green-900/20'
                      : 'text-gray-400 hover:text-green-500 hover:bg-green-50 dark:hover:bg-green-900/20'
                  }`}
                >
                  <ShoppingCart className="w-4 h-4" />
                </button>
              </div>
            </div>

            <div className="flex items-center space-x-4 text-sm text-gray-600 dark:text-gray-400 mb-3">
              <div className="flex items-center space-x-1">
                <Star className="w-4 h-4 text-yellow-500 fill-current" />
                <span>{course.rating}</span>
                <span>({formatNumber(course.reviewsCount)})</span>
              </div>
              <div className="flex items-center space-x-1">
                <Clock className="w-4 h-4" />
                <span>{course.duration}</span>
              </div>
              <div className="flex items-center space-x-1">
                <Users className="w-4 h-4" />
                <span>{formatNumber(course.studentsEnrolled)} students</span>
              </div>
            </div>

            <div className="flex justify-between items-center">
              <div className="flex items-center space-x-2">
                <span className="text-lg font-bold text-gray-900 dark:text-white">
                  {formatPrice(course.price)}
                </span>
                {course.originalPrice > course.price && (
                  <span className="text-sm text-gray-500 line-through">
                    {formatPrice(course.originalPrice)}
                  </span>
                )}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                {course.level} • {course.language}
              </div>
            </div>
          </div>
        </Link>
      </motion.div>
    )
  }

  return (
    <motion.div
      whileHover={{ y: -4 }}
      className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 overflow-hidden hover:shadow-xl transition-all duration-300"
    >
      <Link to={`/course/${course.id}`}>
        <div className="relative">
          <img
            src={course.thumbnail || course.image}
            alt={course.title}
            className="w-full h-48 object-cover"
          />
          
          {/* Badges */}
          <div className="absolute top-3 left-3 flex flex-col space-y-1">
            {course.isBestseller && (
              <div className="bg-yellow-500 text-white text-xs px-2 py-1 rounded flex items-center space-x-1">
                <TrendingUp className="w-3 h-3" />
                <span>Bestseller</span>
              </div>
            )}
            {course.isNew && (
              <div className="bg-green-500 text-white text-xs px-2 py-1 rounded">
                New
              </div>
            )}
            {course.discountPercentage > 0 && (
              <div className="bg-red-500 text-white text-xs px-2 py-1 rounded">
                {course.discountPercentage}% OFF
              </div>
            )}
          </div>

          {/* Action Buttons */}
          <div className="absolute top-3 right-3 flex flex-col space-y-1">
            <button
              onClick={handleWishlistToggle}
              className={`p-2 rounded-full transition-colors ${
                isInWishlist(course.id)
                  ? 'text-red-500 bg-white dark:bg-gray-800'
                  : 'text-gray-400 bg-white dark:bg-gray-800 hover:text-red-500'
              }`}
            >
              <Heart className="w-4 h-4" fill={isInWishlist(course.id) ? 'currentColor' : 'none'} />
            </button>
            <button
              onClick={handleAddToCart}
              disabled={isInCart(course.id)}
              className={`p-2 rounded-full transition-colors ${
                isInCart(course.id)
                  ? 'text-green-500 bg-white dark:bg-gray-800'
                  : 'text-gray-400 bg-white dark:bg-gray-800 hover:text-green-500'
              }`}
            >
              <ShoppingCart className="w-4 h-4" />
            </button>
          </div>

          {/* Play Button Overlay */}
          <div className="absolute inset-0 bg-black bg-opacity-0 hover:bg-opacity-20 transition-all duration-200 flex items-center justify-center">
            <Play className="w-12 h-12 text-white opacity-0 hover:opacity-100 transition-opacity" />
          </div>
        </div>

        <div className="p-4">
          <h3 className="font-semibold text-gray-900 dark:text-white mb-2 line-clamp-2">
            {course.title}
          </h3>
          
          <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
            {course.instructor}
          </p>

          <div className="flex items-center space-x-2 mb-3">
            <div className="flex items-center space-x-1">
              <Star className="w-4 h-4 text-yellow-500 fill-current" />
              <span className="text-sm font-medium">{course.rating}</span>
            </div>
            <span className="text-sm text-gray-500">({formatNumber(course.reviewsCount)})</span>
          </div>

          <div className="flex items-center justify-between text-sm text-gray-600 dark:text-gray-400 mb-3">
            <div className="flex items-center space-x-1">
              <Clock className="w-4 h-4" />
              <span>{course.duration}</span>
            </div>
            <div className="flex items-center space-x-1">
              <Users className="w-4 h-4" />
              <span>{formatNumber(course.studentsEnrolled)}</span>
            </div>
          </div>

          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <span className="text-lg font-bold text-gray-900 dark:text-white">
                {formatPrice(course.price)}
              </span>
              {course.originalPrice > course.price && (
                <span className="text-sm text-gray-500 line-through">
                  {formatPrice(course.originalPrice)}
                </span>
              )}
            </div>
            <div className="text-xs text-gray-500">
              {course.level}
            </div>
          </div>
        </div>
      </Link>
    </motion.div>
  )
} 